:root {
  --orange: #ee6937;
  --light-orange: #f9d9b6;
  --white: #ffffff;
  --transparent: #ffffff00;
  --dark-blue: #23225e;
  --blue: #3026f1;
  --gray: #f2f2f2;
}

/* Remove controls form input type number */

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox  */
input[type='number'] {
  -moz-appearance: textfield;
}
/* End Remove controls form input type number */

.f-m-order-container {
  padding-top: 70px;
  padding-bottom: 70px;
  background-color: var(--gray);
}
.f-m-order-inner-container {
  background-color: var(--white);
  padding:80px;
  border-radius: 20px;
}
.f-m-order-container-child{
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: var(--white);
  border-radius: 20px;
}
.f-m-order-container-heading {
  font-size: 44px;
  color: var(--dark-blue);

}
.f-m-order-container-description {
  font-size: 18px;
  color: var(--dark-blue);

  line-height: 25px;
}
.f-m-order-tracking-container {
  width: 50%;
  margin: auto;
  background-color: var(--blue);
  display: flex;
  padding: 2px;
  border-radius: 40px;
  position: relative;
}
.f-m-order-tracking-input {
  padding: 10px;
  font-size: 18px;
  color: var(--white);
  border: none;
  padding-left: 25px;
  width: 80%;
  background: var(--transparent);
  border-top-left-radius: 40px;
  outline: none;
  border-bottom-left-radius: 40px;
}
.f-m-order-tracking-input:active {
  outline: none;
}
.f-m-order-tracking-button {
  color: var(--blue);
  background-color: var(--white);
  border-radius: 40px;
  border: none;
  cursor: pointer;
  transition: 0.5s;
  width: 20%;
  padding: 10px;
}
.f-m-order-section-2-container {
  background-color: var(--white);
  margin-top: 10px;
  padding: 100px;
}
.f-m-order-section-2-container2 {
  background-color: var(--gray);
  margin-top: 10px;
  padding: 100px;
}

@media screen and (max-width: 912px) and (orientation: portrait) {
  .f-m-order-container {
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 100px;
    padding-bottom: 100px;
    margin-top: 0px;
    background-color: var(--gray);
  }
  .f-m-order-inner-container {
    padding: 10px;
  }
  .f-m-order-container-child{
  padding-left: 50px;
  padding-right: 50px;
  }
  .f-m-order-container-heading {
    font-size: 38px;
    color: var(--dark-blue);
  }
  .f-m-order-tracking-container {
    width: 100%;
  }
  .f-m-order-tracking-input {
    width: 60%;
  }
  .f-m-order-tracking-button {
    width: 40%;
  }
  .f-m-order-section-2-container {
    padding: 10px;
  }
}
@media screen and (max-width: 500px) and (orientation: portrait) {
  .f-m-order-section-2-container2 {
    background-color: var(--gray);
    margin-top: 10px;
    padding: 10px !important;
  }
  .f-m-order-section-2-container {
    background-color: var(--white);
    margin-top: 10px;
    padding: 10px !important;
    padding-bottom: 20px;
  }
  .postion-btn0-padding{
    text-align: center !important;
  }
}