$padding: 500;
@while $padding >= 0 {
  .p-t-#{$padding} {
    padding-top: #{$padding}px;
  }
  .p-r-#{$padding} {
    padding-right: #{$padding}px;
  }
  .p-l-#{$padding} {
    padding-left: #{$padding}px;
  }
  .p-b-#{$padding} {
    padding-bottom: #{$padding}px;
  }
  .p-v-#{$padding} {
    padding-top: #{$padding}px;
    padding-bottom: #{$padding}px;
  }
  .p-h-#{$padding} {
    padding-right: #{$padding}px;
    padding-left: #{$padding}px;
  }
  .padding-#{$padding} {
    padding: #{$padding}px;
  }

  $padding: $padding - 5;
}
