.card {
  box-shadow: 0 0px 15px rgba(0, 0, 0, 0.10), 0 0px 2px rgba(0, 0, 0, 0.23) !important;
  border-radius: 15px !important;
  margin-bottom: 35px !important;
}

.card-content {
  padding: 0px;
}

.card.transparent {
  background-color: #0000002b;
  color: #ffffff;
}

