/*--- Buttons ---*/
.btn-fill {
  font-weight: 500;
  font-size: 16px;
  position: relative;
  z-index: 2;
  padding: 10px 27px;
  border-radius: 4px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background-color: #ee6937;
  border: 2px solid;
  border-color: #ee6937;
  color: #ffffff;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.btn-fill i {
  margin-top: 2px;
  line-height: 1;
  margin-left: 12px;
  -webkit-transform: translateX(0px);
  -ms-transform: translateX(0px);
  transform: translateX(0px);
  color: #ffffff;

  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.btn-fill i:before {
  font-size: 18px;
}

.btn-fill:hover {
  color: #ee6937;
  background-color: transparent;
}

.btn-fill:hover i {
  color: #ee6937;
  -webkit-transform: translateX(5px);
  -ms-transform: translateX(5px);
  transform: translateX(5px);
}

.btn-fill:focus {
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.btn-fill.btn-gradient {
  border: 0;
  padding: 12px 28px;
}

.btn-fill.btn-gradient:after {
  position: absolute;
  content: '';
  height: 100%;
  width: 100%;
  right: inherit;
  left: 0;
  top: 0;
  z-index: -1;
  border-radius: 4px;
  background: #ee6937;
  background-image: -o-linear-gradient(30deg, #ee6937 0%, #d45424 100%);
  background-image: linear-gradient(60deg, #ee6937 0%, #d45424 100%);
  opacity: 1;
  visibility: visible;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.btn-fill.btn-gradient:before {
  position: absolute;
  content: '';
  height: 100%;
  width: 100%;
  right: inherit;
  left: 0;
  top: 0;
  z-index: -1;

  border-radius: 4px;
  background: #ee6937;
  background: -o-linear-gradient(205deg, #ee6937 40%, #d45424 100%);
  background: linear-gradient(245deg, #ee6937 40%, #d45424 100%);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.5s ease-in-out;

  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.btn-fill.btn-gradient:hover {
  color: #ffffff;
}

.btn-fill.btn-gradient:hover i {
  color: #ffffff;
}

.btn-fill.btn-gradient:hover:after {
  opacity: 0;
  visibility: hidden;
}

.btn-fill.btn-gradient:hover:before {
  opacity: 1;
  visibility: visible;
}

