.btn-ghost.btn-gradient2 {
  color: #000000;
  border: 0;
  padding: 12px 35px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid transparent;
  background-clip: padding-box, border-box;
  background-origin: padding-box, border-box;
  background-image: -o-linear-gradient(#fff, #fff),
    -o-linear-gradient(205deg, #00d7f7, #2671fb);
  background-image: linear-gradient(#fff, #fff),
    linear-gradient(245deg, #00d7f7, #2671fb);
  position: relative;
  z-index: 1;
}

.btn-ghost.btn-gradient2:before {
  content: '';
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  background-image: -o-linear-gradient(205deg, #00d7f7, #2671fb);
  background-image: linear-gradient(245deg, #00d7f7, #2671fb);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.btn-ghost.btn-gradient2:hover {
  color: #ffffff;
}

.btn-ghost.btn-gradient2:hover:before {
  opacity: 1;
  visibility: visible;
}
