.btn-watch {
  width: 220px;
  font-weight: 500;
  font-size: 16px;
  position: relative;
  z-index: 2;
  padding: 5px 15px;
  border-radius: 50px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background-color: transparent;
  color: #fff !important;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  text-decoration: none !important;
}

.item-icon {
  background: #fff !important;
  color: #ee6937 !important;
  padding: 14px 17px;
  margin-right: 10px;
  border-radius: 50%;
  -webkit-transition: all 0.5s ease-in-out !important;
  -o-transition: all 0.5s ease-in-out !important;
  transition: all 0.5s ease-in-out !important;
}

.item-icon:hover {
  /* background: #6f42c1 !important; */
  background-image: linear-gradient(#191847, var(--blue)) !important;
  color: #fff !important;
  -webkit-transition: all 0.5s ease-in-out !important;
  -o-transition: all 0.5s ease-in-out !important;
  transition: all 0.5s ease-in-out !important;
}

.main-carousel {
  width: 100%;
  background: #ee6931;
  border-radius: 0px 0 70px 70px;
}

.item-icon {
  color: #ffffff;
  background-color: #ee6931;
}

.item-figure:before {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  content: '';
  width: 0;
  height: 0;
  background-color: rgba(255, 255, 255, 0.3);
  opacity: 1;
  -webkit-transition: all 0.8s ease-in-out;
  -o-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
}

.item-figure:after {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  content: '';
  width: 0;
  height: 0;
  background-color: rgba(255, 255, 255, 0.3);
  opacity: 1;
  -webkit-transition: all 0.8s ease-in-out;
  -o-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
}

.item-figure:hover::after {
  width: 100%;
  height: 100%;
  opacity: 0;
}
.item-figure:hover::before {
  width: 100%;
  height: 100%;
  opacity: 0;
}

