.my-news-content-hide {
  overflow: hidden;
  text-overflow: ellipsis;
  transition: 0.5s ease-in-out !important;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.my-card-content-hide {
  overflow: hidden;
  text-overflow: ellipsis;
  transition: 0.5s ease-in-out !important;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  min-height: 83px;
}
.my-news-title-hide {
  height: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: 0.5s ease-in-out !important;
}
.border-remove {
  background-color: transparent !important;
  color: orangered !important;
}
.border-remove::before {
  border: none !important;
  color: orangered !important;
}
.border-remove::after {
  border: none !important;
  background-color: transparent !important;
  color: orangered !important;
}

