* {
  box-sizing: border-box;
}

:root {
  --primary: #ee6937;
  --black: #000000;
  --secondary: #3745f1;
  --peach: #f9d9b6;
  --white: #fff;
  --grey: #f1f4f8b0;
  --dark-grey: #6b7c93;
  --green: #24b47e;
  --teal: #4f96ce;
  --blue: #191847;
  --dark-blue: #23225e;
  --spacer: 28px;
}

body {
  font-family: -apple-system, system-ui, 'Segoe UI', 'Roboto', 'Helvetica Neue',
    Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  color: var(--dark-grey);
}

body a:hover {
  color: var(--blue);
}
body a {
  color: var(--primary);
}

a {
  cursor: pointer;
  text-decoration: none;
  color: var(--blue);
}

a:hover,
a:focus {
  color: var(--dark-blue);
}

ul {
  list-style: none;
  padding-left: 0;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}
