.full-width-dialog {
  & > div:last-child > div {
    width: 100%;
  }
}
.full-height-dialog {
  & > div:last-child > div {
    height: 100%;
  }
}

.dialogaction {
  display: flex;

  & > div:first-child {
    flex: 1;
  }
}

