.thumb {
  &-img {
    margin-right: 10px;
    width: 32px;
    height: 32px;

    & > img {
      width: 32px;
      height: 32px;
      border: 1px solid rgba(52, 152, 219, 0.25);
      border-radius: 3px;
      object-fit: contain;
    }
  }

  &-round {
    border-radius: 50% !important;
  }
}
