  font-weight: normal;
  margin-bottom: 60px;
}

.is-text-blue {
  color: #0aabde;
}

.is-text-green {
  color: #61bd54;
}

.title.is-3 {
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
  line-height: 50px;
}

.title.is-4 {
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
}

.title.is-5 {
  line-height: 30px;
  letter-spacing: 1px;
  font-weight: 600;
  color: #61bd54;
  font-size: 20px !important;
}

.title.is-6 {
  font-weight: 600;
}

p {
  font-family: 'Raleway-Regular';
  font-weight: 500;
  color: #191847;
  line-height: 20px;
  font-size: 16px;
}

p.semibold {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
}

span.help-block {
  color: red;
}

// Slider.css

.slider-title {
  font-size: 40px !important;
}

.slider-description {
  font-size: 14px !important;
}

