:root {
  --orange: #ee6937;
  --light-orange: #f9d9b6;
  --white: #ffffff;
  --transparent: #ffffff00;
  --dark-blue: #23225e;
  --blue: #3026f1;
  --gray: #f2f2f2;
}

.c-return-banner-1-container {
  margin-top: 150px;
  padding-left: 100px;
  padding-right: 100px;
  padding-bottom: 30px;
}
.c-return-banner-1-heading {
  color: var(--orange);
  font-size: 36px;
  padding-bottom: 30px;
}
.c-return-banner-1-description {
  font-size: 18px;
  color: var(--orange);
  line-height: 25px;
  
}
.c-return-banner-1-highlight-description {
  font-size: 20px;
  line-height: 25px;
  color: var(--orange);
  font-weight: bolder !important;
}
.c-return-banner-1-image {
  width: 100%;
  height: 55vh;
  padding-top: 0px;
  object-fit: contain;
}
/* Mobile Media query for banner 1  */
@media screen and (max-width: 912px) and (orientation: portrait) {
  .c-return-banner-1-container {
    margin-top: 150px;
    padding-left: 30px;
    padding-right: 30px;
  }
  .c-return-banner-1-heading {
    font-size: 40px;
  }
  .c-return-banner-1-description {
    font-size: 16px;
    line-height: 22px;
  }
  .c-return-banner-1-highlight-description {
    font-size: 16px;
    line-height: 22px;
  }
  .c-return-banner-1-image {
    /* width: 100%; */
    height: 80vh;
    object-fit: contain;
  }
}
@media screen and (max-width: 500px) and (orientation: portrait) {
  .c-return-banner-1-container {
    margin-top: 50px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 50px;
  }
  .c-return-banner-1-heading {
    padding-bottom: 20px;
  }
  .c-return-banner-1-image {
    width: 100%;
    height: 40vh;
    padding-top: 0px;
    object-fit: contain;
  }
  .demo-button{
padding-bottom: 50px;
  }
}
/* End Mobile Media query for banner 1  */

.c-return-banner-2-container {
  background-color: var(--gray);
  margin-top: 10px;
  padding-top: 100px;
  padding-bottom: 100px;
}
.c-return-banner-2-heading {
  margin-top: 40px;
  font-size: 42px;
  color: var(--dark-blue);
}
.c-return-banner-2-content-paragraph {
  margin-top: 30px;
  font-size: 18px;
  color: var(--dark-blue);
  line-height: 25px;
}
.c-return-banner-2-image {
  width: 100%;
  height: 400px;
  object-fit: contain;
}
/* Mobile Media query for banner 2  */
@media screen and (max-width: 912px) and (orientation: portrait) {
  .c-return-banner-2-container {
    background-color: var(--gray);
    margin-top: 0px;
    padding: 10px;
  }
  .c-return-banner-2-heading {
    font-size: 36px;
    color: var(--dark-blue);
  }
  .c-return-banner-2-content-paragraph {
    margin-top: 50px;
    font-size: 16px;
    color: var(--dark-blue);
    line-height: 22px;
  }
  .c-return-banner-2-image {
    width: 100%;
    height: 400px;
    object-fit: contain;
  }
}
/* End Mobile Media query for banner 2  */

.c-return-key-addribute-container {
  padding: 50px;
  margin-top: 30px;
}
.c-return-key-att-box {
  -webkit-box-shadow: 0px 0px 10px 0px rgba(140, 140, 140, 1);
  -moz-box-shadow: 0px 0px 10px 0px rgba(140, 140, 140, 1);
  box-shadow: 0px 0px 10px 0px rgba(140, 140, 140, 1);
  border-radius: 30px;
  padding: 20px;
  background-color: var(--white);
  position: relative;
}
.c-return-key-att-box-image {
  height: 50px;
  width: 100%;
  object-fit: contain;
}
.c-return-key-att-box-heading {
  margin-top: 40px;
  font-size: 30px;
  color: var(--dark-blue);
}
.c-return-key-att-box-description {
  margin-top: 30px;
  margin-bottom: 50px;
  font-size: 16px;
  line-height: 25px;
  color: var(--dark-blue);
  height: 150px;
}
.c-return-key-att-box-inner-container {
  height: 100%;
}
.c-return-key-att-heading {
  margin-bottom: 50px;
  font-size: 44px;
  color: var(--dark-blue);
}
/* .c-return-key-att-box-learn-container {
  position: absolute;
  bottom: 0;
} */
.c-return-key-att-box-learn-text {
  font-size: 18px;
  color: var(--blue);
  font-weight: bolder !important;
  cursor: pointer;
  transition: 0.5s;
}
.c-return-key-att-box-learn-text:hover {
  color: var(--orange);
  transition: 0.5s;
}

/* Mobile Media query for Key Attributes  */
@media screen and (max-width: 912px) and (orientation: portrait) {
  .c-return-key-addribute-container {
    padding: 20px;
    margin-top: 10px;
    transition: 0.5s;
  }
  .c-return-key-att-heading {
    margin-bottom: 10px;
    font-size: 36px;
  }
  .c-return-key-att-box-image {
    height: 70px;
    width: 100%;
    object-fit: contain;
  }
  .c-return-key-att-box-heading {
    margin-top: 30px;
    font-size: 24px;
  }
  .c-return-key-att-box-description {
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 14px;
    line-height: 20px;
  }
  .c-return-key-att-box-learn-text {
    font-size: 14px;
    transition: 0.5s;
  }
}
/* End Mobile Media query for Key Attributes  */
