.myScrollbarY::-webkit-scrollbar {
    width: 6px;
}
 
/* Track */
.myScrollbarY::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    -webkit-border-radius: 10px;
    border-radius: 10px;
}
 
/* Handle */
.myScrollbarY::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
.myScrollbarY::-webkit-scrollbar-thumb:window-inactive {
	background: rgba(255,0,0,0.4); 
}

.myScrollbarX::-webkit-scrollbar {
    width: 0px;
}

.onHoverTableRow:hover {
    background-color: #e1e1e1;
    /* background-color: #d3d9ef; */
}

