:root {
  --orange: #ee6937;
  --light-orange: #f9d9b6;
  --white: #ffffff;
  --transparent: #ffffff00;
  --dark-blue: #23225e;
  --blue: #3026f1;
  --gray: #f2f2f2;
  --peach: #f9d9b6;
}

.footer-container {
  width: 100%;
  padding: 50px 100px 30px 80px;
  background-color: var(--peach);
}
.footer {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 20px;
  background-color: var(--peach);
}
.footer-logo {
  height: 70px;
  margin-top: 22px;
  margin-left: 5px;
}
.footer-buttons {
  font-size: 17px;
  color: var(--white);
  background-color: var(--orange);
  border-radius: 40px;
  border: none;
  width: 170px;
  padding: 13px;
  cursor: pointer;
  border: 1px solid var(--transparent);
  transition: 0.4s;
}
.footer-buttons:hover {
  color: var(--orange);;
  background-color: var(--white);
  /* border: 1px solid var(--light-orange); */
  transition: 0.4s;
  /* box-shadow: 2px; */
}
.footer-content-heading {
  color: var(--orange);
  text-transform: uppercase;
}
.footer-content-links {
  color: var(--orange);
  transition: 0.5s;
}
.footer-content-links:hover {
  color: var(--dark-blue);
  transition: 0.5s;
}
.link {
  display: block !important;
  width: fit-content !important;
}
.orange-divider {
  border-top: 2px solid var(--orange);
}
.mobile-application-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.download-label {
  color: var(--orange);
  margin-top: 5px;
  margin-bottom: 0em !important;
}
.icon-style {
  padding-top: 12px;
  padding-bottom: 8px;
  padding-left: 10px;
  padding-right: 10px;
  color: var(--orange);
  transition: 0.5s;
  cursor: pointer;
}
.icon-style:hover {
  color: var(--dark-blue);
  transition: 0.5s;
}
.google-play{
  width: 122px !important;
  height: 42px !important;
}
.app-store{
  width: 122px !important;
  height: 44px !important;
}
/* Media Query of Footer */
@media screen and (max-width: 912px) and (orientation: portrait) {
  .footer-container {
    width: 100%;
    padding: 20px 20px 20px 20px;
    background-color: var(--peach);
  }
  .social-margin-top {
    /* margin-top: 25px !important; */
  }
}
/* End Media Query of Footer */
