body .p-menubar {
  background-color: transparent;
  border: none;

  & .p-menubar-root-list {
    & .p-menuitem {
      & .p-menuitem-link {
        color: #F9D9B6 !important;

        & .p-menuitem-text {
          color: #F9D9B6 !important;
        }
      }
    }

    & .p-menuitem-active {
      & .p-menuitem-link {
        background-color: transparent;

        & .p-submenu-icon {
          color: rgb(250, 120, 22);
        }
      }
    }

    & .p-menuitem-link:hover {
      background-color: transparent;
      color: #ffffff !important;
    }
  }

  & .p-submenu-list {
    & .p-menuitem {
      & .p-menuitem-link {
        color: #000000 !important;

        & .p-menuitem-text {
          color: #000000 !important;
        }
      }
    }
  }
}
