:root {
  --orange: #ee6937;
  --light-orange: #f9d9b6;
  --white: #ffffff;
  --transparent: #ffffff00;
  --dark-blue: #23225e;
  --blue: #3026f1;
  --gray: #f2f2f2;
}

.dom-int-shipping-container {
  margin-top: 100px;
  padding-right: 100px;
  padding-left: 100px;
}
.dom-int-shipping-heading {
  font-size: 44px;
  color: var(--dark-blue);
  text-align: center;
}
.dom-int-shipping-description {
  font-size: 18px;
  color: var(--dark-blue);
  line-height: 25px;
  text-align: center;
  margin-top: 20px;
}
.ship-from-store-card-image-container {
  display: flex;
  justify-content: center;
}
.ship-from-store-card-image {
  width: 100%;
  object-fit: contain;
}
.ship-from-store-button {
  font-size: 20px;
  color: var(--white);
  background-color: var(--orange);
  border-radius: 40px;
  border: none;
  width: 220px;
  padding: 15px 20px 15px 20px;
  cursor: pointer;
  border: 1px solid var(--transparent);
  transition: 0.5s;
  margin-bottom: 10px;
}
.ship-from-store-button:hover {
  color: var(--orange);
  background-color: var(--light-orange);
  transition: 0.5s;
}
@media screen and (max-width: 500px) and (orientation: portrait) {
  .dom-int-shipping-container {
    margin-top: 100px;
    padding-right: 10px;
    padding-left: 10px;
    
  }
  .dom-int-shipping-description {
    text-align: left;
    margin-top: 40px;
  }
  .dom-int-shipping-heading {
    text-align: left;
  }
.mobile-padding{
  padding-left: 20px !important;
  padding-right: 20px !important;
  padding-top: 20px;
}
}