.ds-left{
    background-color: var(--orange);
    padding: 10% 5%;
    text-align: center;
    height: 240px;
}

.ds-left-heading{
    width: 170px;
    color: #ffffff;
    text-align: left;
    display: inline-block;
}

.ds-right-img-container{
    padding: 50% 0;
    height: 100%;
    align-items: center;
}

.ds-right-img{
    width: 80px;
    height: 80px;
}

.ds-right{
    padding-top: 0px;
}

.ds-right-content-container{
    padding: 10% 0;
    height: 100%;
    align-items: center;
    width: 100%;
}

.ds-right-content-heading{
    margin-bottom: 5px !important;
}

.ds-right-content-sub-heading{
    font-size: 15px;
    margin-top: 0px;
}

.ds-right-content-paragraph{
    font-weight: 600;
    font-size: 15px;
    overflow: hidden;
    width: 100%;
}

.ds-right-content-readmore{
    font-weight: 700;
    color: var(--orange);
}