:root {
  --orange: #ee6937;
  --light-orange: #f9d9b6;
  --white: #ffffff;
  --transparent: #ffffff00;
  --dark-blue: #23225e;
  --blue: #3026f1;
  --gray: #f2f2f2;
}

.e-marchant-help-center-banner-1-container {
  margin-top: 100px;
  padding: 100px;
}
.e-marchant-help-center-banner-1-heading {
  font-size: 44px;
  color: var(--orange);
}
.e-marchant-help-center-banner-1-paragraph {
  margin-top: 50px;
  font-size: 18px;
  color: var(--orange);
  line-height: 25px;
}
.e-merchant-hc-heading {
  padding-top: 50px;
  font-size: 44px;
  color: var(--dark-blue);
  text-align: center;
}

/*  Media Query of Help center Banner */
@media screen and (max-width: 912px) and (orientation: portrait) {
  .e-marchant-help-center-banner-1-container {
    margin-top: 130px;
    padding: 20px;
  }
  .e-marchant-help-center-banner-1-heading {
    font-size: 38px;
  }
  .e-marchant-help-center-banner-1-paragraph {
    margin-top: 2px;
    font-size: 14px;
    line-height: 22px;
  }
  .e-merchant-hc-heading {
    padding-top: 10px;
    text-align: center;
    font-size: 34px;
  }
}
/*  End Media Query of Help center Banner */

.e-marchant-hc-container {
  padding-left: 100px;
  padding-right: 100px;
  background-color: var(--gray);
}
.e-marchant-hc-card-container {
  padding-top: 50px;
  padding-bottom: 1px;
}
.e-marchant-hc-card {
  margin-bottom: 10px;
  background: rgb(255 255 255);
  border-radius: 11px;
  padding: 30px;
}

.custom-link {
  color: var(--orange);
  font-weight: 600 !important;
  font-size: 16px;
  transition: 0.5s;
}
.custom-link:hover {
  color: var(--dark-blue) !important;
}
.custom-link:active {
  color: var(--orange) !important;
}
.hc-description {
  font-size: 18px;
}
.hc-image {
  width: 100%;
  height: 100px;
  object-fit: contain;
}

/*  Media Query of Help center FAQ's */
@media screen and (max-width: 912px) and (orientation: portrait) {
  .e-marchant-hc-container {
    padding-left: 10px;
    padding-right: 10px;
    background-color: var(--gray);
  }
  .e-marchant-hc-card-container {
    padding-top: 30px;
    padding-bottom: 1px;
  }

  .custom-link {
    font-size: 14px;
  }
}
/*  End Media Query of Help center FAQ's */
