.home-banner-container {
  height: 90vh;
  width: 100%;
}
.home-banner-content-parent {
  height: 92vh;
  background-color: var(--orange);
  margin: 10px 0 0 -10px;
  -moz-border-radius: 250px / 100px;
  -webkit-border-radius: 250px / 100px;
  border-radius: 250px / 100px;
}
.home-banenr-margin-top {
  padding-top: 200px;
  padding-left: 50px;
  padding-right: 50px;
}
.home-banner-image {
  width: 100%;
  height: 500px;
  object-fit: contain;
}
.home-banner-heading {
  margin-top: 10%;
  font-size: 40px;
  color: var(--light-orange);
}
.home-banner-content-paragraph {
  margin-top: 30px;
  font-size: 18px;
  color: var(--light-orange);
  line-height: 25px;
}
.home-banner-button {
  font-size: 20px;
  color: var(--orange);
  background-color: var(--light-orange);
  border-radius: 40px;
  border: none;
  width: 250px;
  padding: 20px;
  cursor: pointer;
  border: 1px solid var(--transparent);
  transition: 0.5s;
}
.home-banner-button:hover {
  color: var(--light-orange);
  background-color: var(--orange);
  border: 1px solid var(--light-orange);
  transition: 0.4s;
  box-shadow: 2px;
  /* transform: scale(1.04); */
}

/* NavBar button  */
.navbar-button {
  font-size: 17px;
  color: var(--light-orange);
  background-color: var(--orange);
 
  border-radius: 40px;
  border: none;
  width: 200px;
  padding: 20px;
  cursor: pointer;
  border: 1px solid var(--light-orange);
  transition: 0.5s;
}

.navbar-button:hover {
  color: var(--orange);
  background-color: var(--light-orange); 
  border: 1px solid var(--transparent);
  transition: 0.4s;
  box-shadow: 2px;
  /* transform: scale(1.04); */
}

.home-banner-linked-content {
  padding-top: 20px;
  font-size: 15px;
  font-weight: 600;
  text-decoration: underline;
  color: var(--light-orange);
  border-radius: 40px;
  padding: 10px;
  /* left: 60px; */
  cursor: pointer;
}
.home-banner-linked-content-position{
  margin-top: 20px  ;
}
/* Media Query of banenr 1 */
@media screen and (max-width: 912px) and (orientation: portrait) {
  .home-banner-container {
    /* height: 90vh; */
    height: auto !important;
    overflow-x: hidden;
    overflow-y: hidden;
  }
  .home-banner-content-parent {
    height: auto !important;
    background-color: var(--orange);
    margin: 10px 0 0 -10px;
    -moz-border-radius: 250px / 100px;
    -webkit-border-radius: 250px / 100px;
    border-radius: 250px / 100px;
  }
  .home-banner-button {
    font-size: 16px;
    width: 150px;
  }
  .home-banner-button:hover {
    color: var(--light-orange);
    background-color: var(--orange);
    border: 1px solid var(--light-orange);
    transition: 0.4s;
    box-shadow: 2px;
    /* transform: scale(1.04); */
  }
  .home-banner-linked-content {
    font-size: 13px;
    left: 20px;
  }
  .home-banner-content-paragraph {
    font-size: 16px;
  }
}
