.button {
  &.txt {
    &-lg {
      padding: 20px 80px;
      height: 0% !important;
      font-size: 16px;
    }

    &-md {
      font-size: 18px;
    }

    &-sm {
      font-size: 16px;
    }
  }
}

.button.is-rounded {
  color: #ffffff;
  background: #1eaae1;
  border-color: #1eaae1;
  font-family: "Poppins", sans-serif;
  font-weight: 800;
}

.button.is-transparent {
  background: #00000080;
  border: none;
  font-weight: 400;
  padding: 5px 25px;
  letter-spacing: 0px;
}

.button.is-green {
  background-color: #61bd54;
  border-color: #61bd54;
}

.button.is-yellow {
  background-color: #f0bf1c;
  border-color: #f0bf1c;
  padding: 10px 30px;
  letter-spacing: 0px;
}

@include mobile {
  .button {
    &.txt {
      &-lg {
        padding: 15px 45px;
        height: 0% !important;
      }

      &-md {
        font-size: 18px;
      }

      &-sm {
        font-size: 14px;
      }
    }

    &.is-medium {
      font-size: 1rem;
    }
  }
}
