$margin: 500;
@while $margin >= 0 {
  .m-t-#{$margin} {
    margin-top: #{$margin}px;
  }
  .m-r-#{$margin} {
    margin-right: #{$margin}px;
  }
  .m-l-#{$margin} {
    margin-left: #{$margin}px;
  }
  .m-b-#{$margin} {
    margin-bottom: #{$margin}px;
  }
  .m-v-#{$margin} {
    margin-top: #{$margin}px;
    margin-bottom: #{$margin}px;
  }
  .m-h-#{$margin} {
    margin-right: #{$margin}px;
    margin-left: #{$margin}px;
  }
  .margin-#{$margin} {
    margin: #{$margin}px;
  }

  $margin: $margin - 5;
}
