.mobile-input-background-en {
  /* background-image: url('../../upload/mobile-container.png'); */
  /* height: 75px; */
  border: 1px solid #718ffc;
  width: 100%;
  border-radius: 10px;
  font-family: 'Exo2-Medium';
  display: flex;
  align-items: center;
  padding: 5px 1em;
  background-color: #ffffff;
}
.mobile-container {
  display: flex;
  align-items: center;
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
}

.mobile-container-flag {
  height: 30px;
  object-fit: contain;
  padding-left: 8px;
  padding-right: 8px;
}

.mobile-container-caret {
  height: 10px;
  object-fit: contain;
  padding-left: 2px;
  padding-right: 2px;
}

.mobile-container-input-container,
.mobile-container-input {
  flex: 1;
  width: 100%;
  border: none;
  /* padding-top: 2.5px; */
}

.mobile-container-input {
  background-color: transparent;
  /* border: none; */
  color: black;
  font-size: 18px;
}
.mobile-container-input:focus,
.mobile-container-input:active {
  border: none !important;
  outline: none !important;
}
.mobile-container-countrycode {
  background-color: transparent;
  font-family: 'Open Sans';
  border: none;
  color: black;
  font-size: 18px;
  padding-left: 5px;
  padding-right: 5px;
}
.flag-item {
  background-color: white;
  cursor: pointer;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.flag-item:hover {
  background-color: whitesmoke;
}
.mobile-list {
  height: 470px;
  overflow-x: hidden;
  overflow-y: scroll;
}

