    color: rgb(0, 0, 0);
    font-size: 18px;
    w
} */
.mobile-list-width {
  padding: 0px !important;
  width: 100%;
  min-width: 433px;
}
.mobile-flag-icon {
  height: 30px;
  object-fit: contain;
  margin-right: 10px;
  filter: drop-shadow(rgb(0, 0, 0) 0px 1px 2px);
}

@media screen and (max-width: 720px) and (orientation: portrait) {
  .mobile-list {
    height: calc(100% - 95px) !important;
  }
  .mobile-list-width {
    min-width: 100% !important;
  }
}
.cursor-pointer {
  cursor: pointer;
  /* padding-top: 5px; */
}
.cursor-pointer-caret {
  cursor: pointer;
  /* padding-top: 2.5px; */
}

.input-container {
  display: flex;
  width: 100%;
  margin-bottom: 15px;
}

.icon {
  padding: 10px;
  background: dodgerblue;
  color: black;
  min-width: 50px;
  text-align: center;
}

.input-field {
  width: 100%;
  padding: 10px;
  outline: none;
}

.input-field:focus {
  border: 2px solid dodgerblue;
}
