@import 'homepage-style.css';
@import 'footer-style.css';
@import 'news-style.css';
@import 'clients-return-style.css';
@import 'e-merchant-help-center.css';
@import 'ship-from-store-style.css';
@import 'follow-my-order-style.css';
@import 'apply-now-style.css';
@import 'not-found-style.css';
@import 'e-merchant-settings-style.css';
@import 'website-style.css';

:root {
  --orange: #ee6937;
  --light-orange: #f9d9b6;
  --white: #ffffff;
  --transparent: #ffffff00;
  --dark-blue: #23225e;
  --blue: #3026f1;
  --gray: #f2f2f2;
}

.align-vertical-center {
  display: flex;
}
.align-horizontal-center {
  display: flex !important;
  justify-content: center !important;
}
.align--center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.anchor-clickable {
  display: block !important;
  height: 100% !important;
  width: 100% !important;
}
button:focus {
  outline: none !important;
}
.bg-grey {
  background-color: var(--gray);
}

.bg-blue {
  background-color: var(--blue);
}
.Toastify__toast--success {
  background: rgb(0, 120, 172) !important;
}
.Toastify__toast--warning {
  background: #c02e41 !important;
}
.Toastify__toast--error {
  background: #c02e41 !important;
}
.btn-peach {
  align-items: center !important;
  justify-content: center !important;
  padding: 10px !important;
  font-family: sans-serif !important;
  min-width: 160px !important;
  font-size: 1.1rem !important;
  border-radius: 100px !important;
  border: var(--light-orange) !important;
  color: var(--orange) !important;
  background: var(--light-orange) !important;
}
.btn-peach:hover {
  border: var(--light-orange) 1px solid !important;
  color: #fff !important;
  background: var(--orange) !important;
}

.btn-orange {
  align-items: center !important;
  justify-content: center !important;
  padding: 10px !important;
  font-family: sans-serif !important;
  font-size: 1.1rem !important;
  border-radius: 100px !important;
  min-width: 160px !important;
  color: #fff !important;
  border: var(--light-orange) !important;
  background: var(--orange) !important;
}
.btn-orange:hover {
  border: var(--orange) 1px solid !important;
  color: var(--orange) !important;
  background: var(--light-orange) !important;
}

.u-r-details-container > div:nth-child(2) > .commodities-table tbody > tr {
  color: var(--dark-blue) !important;
}
.u-r-details-container > div:nth-child(2) .basic-details-div {
  border: none !important;
  background-color: var(--white) !important;
  -webkit-box-shadow: 10px 10px 5px -1px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 10px 10px 5px -1px rgba(0, 0, 0, 0.1);
  box-shadow: 10px 10px 5px -1px rgba(0, 0, 0, 0.1);
}
.u-r-details-container > div:nth-child(2) .basic-details-div > div:first-child {
  background-color: var(--dark-blue) !important;
}
.u-r-details-container
  > div:nth-child(2)
  .basic-details-div
  > div:first-child
  > span {
  color: var(--white) !important;
}
/* .{
  display: inline-block !important;
} */
.card-lable-head {
  font-weight: bold !important;
}

.my-fixed-class {
  background: white;
  height: 90vh;
  position: fixed;
  top: 122px;
  width: 100%;
}

.my-class {
  background: var(--orange);
  height: 90vh;
  width: 100%;
}
.footer-foot {
  width: 100% !important;
  height: 34px;
  background-color: #fff;
  text-align: center;
}
.container-layout {
  overflow: auto !important;
}
