.navbar {
  .navbar-item {
    text-transform: uppercase;
    padding: 0.5rem 2rem;
    border-radius: 35px;
    font-size: 14px;
  }

  .navbar-item.active {
    background-color: #1eaae1;
    color: #ffffff;
  }
  .navbar-item img {
    max-height: 5.75rem !important;
  }

  .navbar-burger {
    background-color: #1eaae1;
    border-radius: 20px;
  }

  .navbar-burger span {
    color: #ffffff;
  }

  @include desktop {
    // margin: 20px 0px;
    padding: 20px 50px;
  }

  @include mobile {
    padding: 20px 20px;
  }
}
