.apply-now-section {
  width: 100%;
  text-align: center;
  height: 30vh;
  background: rgb(48, 38, 241);
  color: #fff;
}
.apply-now-section p {
  color: #fff;
  padding-bottom: 10px;
}
.apply-now-btn {
  background-color: #fff;
  border: 2px solid #fff;
  border-radius: 50px;
  color: rgb(48, 38, 241);
  padding: 15px;
  width: 200px;
  transition: 0.5s;
}
.apply-now-btn:hover {
  background: rgb(48, 38, 241);
  color: #fff;
  border: 2px solid #fff;
  transition: 0.5s;
}
.margin-top-50 {
  padding-top: 50px;
}
@media screen and (max-width: 500px) and (orientation: portrait) {
  .apply-now-section {
    height: 40vh;
  } 
}
