/*--- Header Common Style ---*/
.temp-logo {
  -webkit-transition: all 1s ease-in-out;

  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.temp-logo a {
  display: block;
}

.header-action-items {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.header-action-items .single-item {
  margin-right: 20px;
  position: relative;
  z-index: 1;
}

.header-action-items .single-item:last-child {
  margin-right: 0;
  padding-right: 0;
}

.header-action-items .single-item:last-child:before {
  display: none;
}

.header-action-items .item-btn {
  padding: 15px 15px;
  margin-top: 4px;
}

.item-btn-active {
  padding: 15px 15px !important;
  margin-top: 4px;
}

.justify-content-end {
  -ms-flex-pack: end !important;
  justify-content: flex-end !important;
}

/*--- Navbar ---*/
nav.template-main-menu {
  font-family: Raleway-Regular;
}

nav.template-main-menu > ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

nav.template-main-menu > ul > li {
  position: relative;
  margin: 0 20px;
}

nav.template-main-menu > ul > li > a {
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  display: block;
  position: relative;
  z-index: 1;
  -webkit-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  font-family: 'Raleway-Bold';
}

.text-color-orange {
  color: #ee6937 !important;
}

nav.template-main-menu > ul > li > a:before {
  position: absolute;
  z-index: 1;
  content: '';
  height: 2px;
  background-color: #ffffff;
  bottom: 5px;
  width: 0%;
  left: 0;
  right: 0;
  margin: 0 auto;
  -webkit-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

nav.template-main-menu > ul > li > a:hover:before {
  width: 50%;
}

nav.template-main-menu > ul > li ul.dropdown-menu-col-1 {
  padding-top: 5px;
  padding-left: 5px;
  padding-bottom: 5px;
  border-top: 1px solid #ee6937;
  background-color: #ffffff;
  min-width: 190px;
  visibility: hidden;
  opacity: 0;
  position: absolute;
  z-index: 0;
  top: 100%;
  left: -10;
  border-radius: 4px;
  -webkit-transform: translateY(0px);
  -ms-transform: translateY(0px);
  transform: translateY(0px);
  -webkit-box-shadow: 0 0 35px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 35px 0 rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  margin-top: 0px;
}

nav.template-main-menu > ul > li ul.dropdown-menu-col-1:before {
  position: absolute;
  z-index: 1;
  background-color: #ee6937;
  height: 1px;
  border-radius: 4px;
  content: '';
  width: 100%;
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: 0;
}

