  /* breadcumb */
  
  .steps {
    margin: 40px 0;
    padding: 0;
    overflow: hidden;
  }
  .steps a {
    color: white;
    text-decoration: none;
    cursor: text;
    font-family: arial;
    font-size: 12px;
  }
  /* .steps em {
    display: block;
    font-size: 1.1em;
    font-weight: bold;
  } */
  .steps li {
    float: left;
    margin-left: 0;
    /* width: 180px; */ /* 100 / number of steps */
    line-height: 30px;
    list-style-type: none;
    padding: 5px 5px 5px 25px; /* padding around text, last should include arrow width */
    border-right: 3px solid white; /* width: gap between arrows, color: background of document */
    position: relative;
  }
  /* remove extra padding on the first object since it doesn't have an arrow to the left */
  .steps li:first-child {
    padding-left: 25px;
  }
  /* white arrow to the left to "erase" background (starting from the 2nd object) */
  .steps li:nth-child(n + 2)::before {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    border-left: 10px solid white; /* width: arrow width, color: background of document */
    border-top: 20px solid transparent; /* width: half height */
    border-bottom: 20px solid transparent; /* width: half height */
    width: 0;
    height: 0;
    content: ' ';
  }
  /* colored arrow to the right */
  .steps li::after {
    z-index: 1; /* need to bring this above the next item */
    position: absolute;
    top: 0;
    right: -10px; /* arrow width (negated) */
    display: block;
    border-left: 10px solid #1f5024; /* width: arrow width */
    border-top: 20px solid transparent; /* width: half height */
    border-bottom: 20px solid transparent; /* width: half height */
    width: 0;
    height: 0;
    content: ' ';
  }
  
  /* Setup colors (both the background and the arrow) */
  
  /* Completed */
  .steps li {
    background-color: #1f5024;
  }
  .steps li::after {
    border-left-color: #1f5024;
  }
  .steps li span.number {
    display: none;
  }
  
  /* Current */
  .steps li.current {
    background-color: #3db319;
  }
  .steps li.current::after {
    border-left-color: #3db319;
  }
  .steps li.current span.number {
    display: inline-block;
  }
  .steps li.current i.fa.fa-check {
    display: none;
  }
  
  /* Following */
  .steps li.current ~ li {
    background-color: #eaeaea;
  }
  .steps li.current ~ li::after {
    border-left-color: #eaeaea;
  }
  .steps li.current ~ li a {
    color: #656567;
  }
  .steps li.current ~ li span.number {
    display: inline-block;
  }
  .steps li.current ~ li i.fa.fa-check {
    display: none;
  }
  
  /* Hover for completed and current */
  /* .steps li:hover {background-color: #696}
  .steps li:hover::after {border-left-color: #696} */
  
  @media screen and (max-width: 800px) {
    /* body {
              background-color: lightgreen;
    } */
    .steps li {
      width: 75px; /* 100 / number of steps */
      height: 50px; /* total height */
      line-height: 22px;
      padding: 5px 5px 5px 15px;
    }
    /* remove extra padding on the first object since it doesn't have an arrow to the left */
    .steps li:first-child {
      padding-left: 10px;
      line-height: 46px;
    }
    /* white arrow to the left to "erase" background (starting from the 2nd object) */
    .steps li:nth-child(n + 2)::before {
      border-top: 30px solid transparent; /* width: half height */
      border-bottom: 30px solid transparent; /* width: half height */
    }
    /* colored arrow to the right */
    .steps li::after {
      border-top: 30px solid transparent; /* width: half height */
      border-bottom: 30px solid transparent; /* width: half height */
    }
  }
  
