  .box-container-vehicle {
    /* display: inline-block;
    vertical-align: middle; */
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.2s;
    transition-duration: 0.2s;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
  }
  .box-container-vehicle:hover, .box-container-vehicle:focus, .box-container-vehicle:active {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
    -webkit-box-shadow: 10px 10px 5px -1px rgba(0,0,0,0.1);
    -moz-box-shadow: 10px 10px 5px -1px rgba(0,0,0,0.1);
    box-shadow: 10px 10px 5px -1px rgba(0,0,0,0.1);
  }
